
#map {
  width: 100%;
  height: 200px;
}
@media screen and (min-width: 480px) {
#map {
      height: 350px;
}
}
.titleColor {
  color: #ffba41;
}
.bestBookList {
  color: #3fa4f6;
}
.thumbnailArea {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background-color: white;
}
.thumbnailArea h2 {
    font-weight: 200;
    margin-top: 0.8rem;
    margin-bottom: 0;
}
.thumbnailArea p {
    margin-top: 0.2rem;
    margin-bottom: 15px;
    text-align: center;
    color: #afaaaa;
}
@media screen and (max-width: 480px) {
.thumbnailArea {
      width: 95%;
      margin: 10px auto;
}
.thumbnailArea h2 {
        font-size: 1.3rem;
}
.thumbnailArea p {
        width: 170px;
        font-size: 0.8rem;
}
.thumbnailArea .imageSize {
        width: 190px;
        height: 190px;
        border-radius: 100%;
        margin-top: 15px;
        overflow: hidden;
}
.thumbnailArea .imageSize img {
          width: 300px;
}
}
@media screen and (min-width: 480px) {
.thumbnailArea {
      width: 1110px;
      margin: 25px auto;
      padding: 30px 0;
}
.thumbnailArea h2 {
        font-size: 1.8rem;
}
.thumbnailArea p {
        font-size: 0.8rem;
        margin-top: 10px;
}
.thumbnailArea .imageSize {
        width: 250px;
        height: 250px;
        border-radius: 100%;
        margin-top: 15px;
        overflow: hidden;
}
.thumbnailArea .imageSize img {
          width: 400px;
}
}
@media screen and (min-width: 480px) {
.whiteBoxControll {
    width: 1110px;
    margin: 0 auto;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
}
}
.whiteBox {
  background-color: white;
}
.whiteBox h3 {
    margin: 0;
    margin-bottom: 10px;
}
.whiteBox p {
    color: #afaaaa;
}
@media screen and (max-width: 480px) {
.whiteBox {
      width: 90%;
      margin: 10px auto;
      padding: 10px;
}
.whiteBox h3 {
        font-weight: 100;
}
.whiteBox p {
        width: 95%;
        margin: 0 auto;
}
}
@media screen and (min-width: 480px) {
.whiteBox {
      box-sizing: border-box;
      width: 540px;
      padding: 30px;
      margin-bottom: 25px;
}
.whiteBox h3 {
        font-weight: 100;
}
.whiteBox p {
        width: 95%;
        margin: 0 auto;
}
}
.bookStoreImage {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.storeThumbnail {
  overflow: hidden;
  margin: 5px;
}
@media screen and (max-width: 480px) {
.storeThumbnail {
      width: 75px;
      height: 75px;
}
.storeThumbnail img {
        height: 100px;
}
}
@media screen and (min-width: 480px) {
.storeThumbnail {
      width: 110px;
      height: 110px;
      margin-bottom: 5px;
}
.storeThumbnail img {
        height: 150px;
}
}



