
.footer {
  background: #ffba41;
}
.footer button {
    display: block;
    background: #ffba41;
    border: 0;
    cursor: pointer;
    margin: 0 auto;
}
@media screen and (max-width: 480px) {
.footer {
      padding: 10px 0 5px 0;
}
}
@media screen and (min-width: 480px) {
.footer {
      padding: 30px 0 10px 0;
}
}
.footerIcon {
  border: 2px solid white;
  border-radius: 100%;
  width: 50px;
  height: 50px;
}
.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}
.modalWindow {
  position: fixed;
  border-radius: 3px;
  top: 40%;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  background-color: white;
}
@media screen and (max-width: 480px) {
.modalWindow {
      width: 70%;
      padding: 10px;
      padding-top: 22px;
}
}
@media screen and (min-width: 480px) {
.modalWindow {
      width: 30%;
      padding: 15px;
      padding-top: 25px;
}
}
.modalWindow h4 {
    margin-top: 15px;
    margin-bottom: 0;
}
.modalWindow p {
    margin-bottom: 10px;
    margin-top: 7px;
    font-size: 0.9rem;
    color: #757575;
}
.modalWindow button {
    position: absolute;
    top: 0;
    right: 0;
    background: white;
}
.modalContents {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.modalContents a {
    border: 3px solid #757575;
    border-radius: 50%;
    padding: 9px 11px;
    color: #757575;
}
.fade-enter-active, .fade-leave-active {
  transition: opacity 0.7s ease;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}



