
.mainTitle {
  color: white;
  margin: 0;
}
@media screen and (max-width: 480px) {
.mainTitle {
      font-weight: 700;
      font-size: 1.6rem;
      line-height: 50px;
}
}
@media screen and (min-width: 480px) {
.mainTitle {
      font-weight: 500;
      font-size: 2.3rem;
}
}
.mapIcon {
  width: 50px;
  height: 50px;
}
@media screen and (min-width: 480px) {
.mapIcon {
      position: relative;
      top: -10px;
}
}
.header {
  background: #ffba41;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
@media screen and (max-width: 480px) {
.header {
      padding: 0 10px;
      -webkit-box-pack: justify;
          -ms-flex-pack: justify;
              justify-content: space-between;
}
}
@media screen and (min-width: 480px) {
.header {
      padding: 1.5rem 0;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
          -ms-flex-direction: column;
              flex-direction: column;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
}
}



