
@media screen and (min-width: 480px) {
.allBookList {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    padding: 0 12%;
}
}
.listWrap {
  list-style: none;
  padding: 0 10px;
  position: relative;
  margin: 0;
}
@media screen and (min-width: 480px) {
.listWrap {
      background: black;
      cursor: pointer;
      width: 349px;
      padding: 0;
}
.listWrap :hover > .bookStorePhoto {
        opacity: 0.8;
}
.listWrap :hover > .bookStorePhoto img {
          -webkit-animation: imgIncrease 0.5s ease-in-out forwards;
                  animation: imgIncrease 0.5s ease-in-out forwards;
}
}
@media screen and (max-width: 480px) {
.bookStorePhoto {
    margin: 10px 0;
    width: 100%;
    height: 200px;
    overflow: hidden;
    position: relative;
}
.bookStorePhoto img {
      width: 100%;
      position: absolute;
      top: -15%;
}
}
@media screen and (min-width: 480px) {
.bookStorePhoto {
    height: 300px;
    overflow: hidden;
}
.bookStorePhoto img {
      width: 150%;
}
}
.namePosition {
  color: white;
  position: absolute;
  top: 30%;
  left: 50%;
  font-size: 1.5rem;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  letter-spacing: 0.3rem;
}
@-webkit-keyframes imgIncrease {
0% {
    -webkit-transform: scale(1, 1);
            transform: scale(1, 1);
}
100% {
    -webkit-transform: scale(1.1, 1.1);
            transform: scale(1.1, 1.1);
}
}
@keyframes imgIncrease {
0% {
    -webkit-transform: scale(1, 1);
            transform: scale(1, 1);
}
100% {
    -webkit-transform: scale(1.1, 1.1);
            transform: scale(1.1, 1.1);
}
}



